import React from 'react'
import styled from 'styled-components'
import { graphql, Link } from 'gatsby'
import Image from 'gatsby-image'

// Styles
import { desktopVw } from '../styles/utils'

import { getPathName } from '../utils'

import SEO from '../components/SEO'
import TextButton from '../components/TextButton'
import WelcomeModal from '../components/WelcomeModal'

const StyledCategory = styled.section``

const Title = styled.h1`
  z-index: 1;
  position: absolute;
  top: ${props =>
    `calc(${props.theme.header.mobile.height +
      props.theme.announcementBar.mobile.height +
      30}px)`};
  left: 0;
  right: 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 80px;
  font-weight: 500;
  font-family: ${props => props.theme.fonts.secondary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: ${desktopVw(131)};
    font-size: ${desktopVw(220)};
  }
`

const CollectionsMobile = styled.div`
  margin-top: -${props => props.theme.header.mobile.height + 1}px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const CollectionsDesktop = styled.div`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    margin-top: -${props => desktopVw(props.theme.header.desktop.height + 1)};
    margin-bottom: ${desktopVw(82)};
    justify-content: space-between;
    padding-right: ${desktopVw(80)};
  }
`

const iosBar = 69
const FeaturedCollection = styled.article`
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: calc(
    100vh - ${props => props.theme.announcementBar.mobile.height + iosBar}px
  );
  padding: 34px 20px;
  box-sizing: border-box;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: sticky;
    top: 0;
    height: calc(
      100vh + ${props => desktopVw(props.theme.announcementBar.desktop.height)}
    );
    width: calc(50% - ${desktopVw(80)});
    padding: 0 ${desktopVw(80)} ${desktopVw(113)};
    margin-top: -${props => desktopVw(props.theme.announcementBar.desktop.height)};
  }
`

const FeaturedCollectionTitle = styled.h1`
  font-size: 30px;
  margin-bottom: 44px;
  font-weight: 500;
  line-height: 30px;
  text-transform: uppercase;
  color: ${props => props.color};

  span {
    display: block;

    &:nth-child(2) {
      font-size: 37px;
      font-weight: 600;
      font-family: ${props => props.theme.fonts.secondary};
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(30)};
    margin-bottom: ${desktopVw(16)};
    line-height: ${desktopVw(30)};

    span {
      &:nth-child(2) {
        font-size: ${desktopVw(37)};
      }
    }
  }
`

const Shopnow = styled(Link)`
  color: ${props => (props.color ? props.color : props.theme.colors.primary)};
  text-decoration: underline;
  font-size: 16px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(16)};
  }
`

const CollectionList = styled.section`
  margin: 42px 0;
  display: flex;
  flex-direction: column;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    flex-flow: wrap;
    justify-content: space-between;
    margin: ${desktopVw(369)} 0 0;
    width: ${desktopVw(680)};
  }
`

const CollectionsTitle = styled.h3`
  margin-bottom: 26px;
  color: ${props => props.theme.colors.greyLight};
  font-size: 15px;
  font-family: ${props => props.theme.fonts.primary};
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(49)} 0 0;
    text-transform: initial;
    font-size: ${desktopVw(20)};
  }
`

const StyledTextButton = styled(TextButton)`
  font-size: 20px;
  text-align: center;
  margin-bottom: 29px;

  &:last-child {
    margin: 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(71)} 0 0;
    font-size: ${desktopVw(20)};
  }
`

const BottomThumbnails = styled.ul`
  display: flex;
  margin: 10px 0;
  padding: 0 10px;
  overflow-x: scroll;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;

  &::-webkit-scrollbar {
    display: none;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: column;
    flex-flow: wrap;
    align-items: flex-start;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }
`

const ThumbnailItem = styled.li`
  padding-right: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(44)};
    padding: 0;
    flex-shrink: 0;
    width: calc(50% - ${desktopVw(20)});

    &:nth-last-child(-n + 2) {
      margin-bottom: 0;
    }
  }
`

const Thumbnail = styled.article`
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 18px 30px;
  width: 228px;
  height: 303px;
  box-sizing: border-box;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: 100%;
    padding: ${desktopVw(38)} ${desktopVw(62)};
    height: ${desktopVw(426)};
  }
`

const ThumbnailTitle = styled.h1`
  font-size: 20px;
  text-align: center;
  white-space: pre-wrap;
  color: ${props => (props.color ? props.color : props.theme.colors.secondary)};
  text-transform: uppercase;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(20)};
  }
`

const Category = ({ data }) => {
  const page = data.contentfulCategory
  const collections = page.collections
  const welcomePopupData = data.welcomePopup.nodes.filter(
    node => node.category === 'welcomePopup',
  )[0]

  return (
    <>
      <SEO
        title={page.seoTitle}
        description={page.seoDescription}
        type={page.pageType}
        slug={page.slug}
        image={page.metaImage ? page.metaImage.fluid.src : null}
      />
      <WelcomeModal
        heading={welcomePopupData.heading}
        subHeading={welcomePopupData.subHeading}
        klaviyoListId={welcomePopupData.subscribeForm.klaviyoListId}
        successMessage={welcomePopupData.subscribeForm.successMessage.json}
        colorTheme={Math.round(Math.random())}
        subscriptionType={
          'email'
          /*Math.round(Math.random()) === 0 ? 'email' : 'mobile'*/
        }
      />
      <StyledCategory>
        <Title>{page.name}</Title>
        <CollectionsMobile>
          <FeaturedCollection>
            <Link to={getPathName(collections[0].slug)}>
              <Image
                style={{
                  position: 'absolute',
                  left: '0',
                  right: '0',
                  bottom: '0',
                  top: '0',
                  zIndex: '-1',
                }}
                fluid={collections[0].thumbnailImage.fluid}
                alt={collections[0].thumbnailImage.description}
              />
            </Link>
            <FeaturedCollectionTitle color={collections[0].thumbnailTextColor}>
              {collections[0].name.split(' ').map(word => (
                <span key={word}>{word}</span>
              ))}
            </FeaturedCollectionTitle>
            <Shopnow
              to={getPathName(collections[0].slug)}
              color={collections[0].thumbnailTextColor}
            >
              Shop now
            </Shopnow>
          </FeaturedCollection>
          <CollectionList>
            <CollectionsTitle>Shop by Collection</CollectionsTitle>
            {collections.slice(3).map(c => (
              <StyledTextButton key={c.id} to={getPathName(c.slug)}>
                {c.name}
              </StyledTextButton>
            ))}
          </CollectionList>
          <BottomThumbnails>
            {collections.slice(1, 3).map(c => (
              <ThumbnailItem key={c.id}>
                <Link to={getPathName(c.slug)}>
                  <Thumbnail>
                    <Image
                      style={{
                        position: 'absolute',
                        left: '0',
                        right: '0',
                        bottom: '0',
                        top: '0',
                        zIndex: '-1',
                      }}
                      fluid={c.thumbnailImage.fluid}
                      alt={c.thumbnailImage.description}
                    />
                    <ThumbnailTitle color={c.thumbnailTextColor}>
                      {c.name}
                    </ThumbnailTitle>
                  </Thumbnail>
                </Link>
              </ThumbnailItem>
            ))}
          </BottomThumbnails>
        </CollectionsMobile>
        <CollectionsDesktop>
          <FeaturedCollection>
            <Link to={getPathName(collections[0].slug)}>
              <Image
                style={{
                  position: 'absolute',
                  left: '0',
                  right: '0',
                  bottom: '0',
                  top: '0',
                  zIndex: '-1',
                }}
                fluid={collections[0].thumbnailImage.fluid}
                alt={collections[0].thumbnailImage.description}
              />
            </Link>
            <FeaturedCollectionTitle color={collections[0].thumbnailTextColor}>
              {collections[0].name.split(' ').map(word => (
                <span key={word}>{word}</span>
              ))}
            </FeaturedCollectionTitle>
            <Shopnow
              to={getPathName(collections[0].slug)}
              color={collections[0].thumbnailTextColor}
            >
              Shop now
            </Shopnow>
          </FeaturedCollection>
          <CollectionList as='ul'>
            {collections.slice(1).map(c => (
              <ThumbnailItem key={c.id}>
                <Link to={getPathName(c.slug)}>
                  <Thumbnail>
                    <Image
                      style={{
                        position: 'absolute',
                        left: '0',
                        right: '0',
                        bottom: '0',
                        top: '0',
                        zIndex: '-1',
                      }}
                      fluid={c.thumbnailImage.fluid}
                      alt={c.thumbnailImage.description}
                    />
                    <ThumbnailTitle color={c.thumbnailTextColor}>
                      {c.name}
                    </ThumbnailTitle>
                  </Thumbnail>
                </Link>
              </ThumbnailItem>
            ))}
          </CollectionList>
        </CollectionsDesktop>
      </StyledCategory>
    </>
  )
}

export default Category

export const CategoryQuery = graphql`
  query Category($id: String!) {
    contentfulCategory(id: { eq: $id }) {
      id
      name
      seoTitle
      slug
      seoDescription
      metaImage {
        fluid(maxWidth: 1200) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      collections {
        ... on ContentfulCollection {
          id
          name
          slug
          thumbnailImage {
            description
            fluid(maxWidth: 960) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
          thumbnailTextColor
        }
        ... on ContentfulComingSoon {
          id
          slug
          name
          thumbnailImage {
            description
            fluid(maxWidth: 960) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
          thumbnailTextColor
        }
      }
    }

    welcomePopup: allContentfulWelcomePopup {
      nodes {
        id
        heading
        subHeading
        category
        subscribeForm {
          klaviyoListId
          successMessage {
            json
          }
        }
      }
    }
  }
`
